import { Component } from '@angular/core';
import { NavController, LoadingController, ToastController } from 'ionic-angular';
import { AuthServiceProvider } from '../../providers/auth-service/auth-service';

@Component({
  selector: 'page-user-anzeigen',
  templateUrl: 'user-anzeigen.html',
})
export class UserAnzeigenPage {
	user={_id:'', name:'', email:'',rfid:'', voiceId:'',role:'', blocked:false};

 
	
  constructor(public navCtrl: NavController, public authService: AuthServiceProvider, 
  public loadingCtrl: LoadingController, private toastCtrl: ToastController) {
		this.showUser();
		
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad UserAnzeigenPage');
  }
  
  showUser() {
	this.authService.getUser(localStorage.getItem('otherUserId'))
	  .subscribe(data => {
	  this.user = data.data[0]; console.log(this.user);});	
  }

  onChange(){
	if(this.user.blocked == false){
		this.user.blocked = true;
	} else{
		this.user.blocked = false;
	}
  }

  	updateUser() {
		console.log(this.user._id);
		this.authService.updateUser(this.user._id, this.user);
  }
  
    presentToast(msg) {
    let toast = this.toastCtrl.create({
      message: msg,
      duration: 3000,
      position: 'bottom',
      dismissOnPageChange: true
    });

    toast.onDidDismiss(() => {
      console.log('Dismissed toast');
    });
    toast.present();
  }
  
	 
}



