
import { Component } from '@angular/core';
import { NavController, LoadingController, ToastController } from 'ionic-angular';
import { AuthServiceProvider } from '../../providers/auth-service/auth-service';
import { UserAnzeigenPage } from '../user-anzeigen/user-anzeigen';


@Component({
   selector: 'page-user-verwaltung',
  templateUrl: 'user-verwaltung.html',
})
export class UserVerwaltungPage {

  loading: any;
  regData = {name:'', email:'', password:'', rfid:'', voiceId:'', role:'', blocked:false};
  data: any;
  users: any;
  user: any[];
  idOtherUser: any;

  constructor(public navCtrl: NavController, public authService: AuthServiceProvider, 
  public loadingCtrl: LoadingController, private toastCtrl: ToastController) {
	this.regData.role = "user";
	this.getUsers();
  }
	doRegister(){
		this.authService.register(this.regData);
		this.getUsers();
	}
	getUsers() {
	 this.users = this.authService.getAllUsers()
	  .subscribe(data => {this.users = data; });
	}
	
	goToShowUser(user){
		localStorage.setItem('otherUserId',user._id);
		console.log("Ici "+ localStorage.getItem('otherUserId'));
		this.navCtrl.push(UserAnzeigenPage); 
	}
  
showLoader(){
    this.loading = this.loadingCtrl.create({
        content: 'Authenticating...'
    });

    this.loading.present();
  }
	ionViewWillEnter() {
        this.getUsers();
		console.log("I am back"); 
	}
  presentToast(msg) {
    let toast = this.toastCtrl.create({
      message: msg,
      duration: 3000,
      position: 'bottom',
      dismissOnPageChange: true
    });

    toast.onDidDismiss(() => {
      console.log('Dismissed toast');
    });

    toast.present();
  }

}


